import icon from '../icons/aoi.webp'
import lul from '../emotes/lul.webp'
import pekoraSalute from '../emotes/pekoraSalute.webp'
import image from '../images/10.png'


export default (
  <>
  <div className={'en'}>
    <strong>Advent Calendar 2021 Day 10</strong>
  </div>
  <div className={'en'}>
    <p>Greetings everyone! Aoi here.<br />
    How are yall doin? Its getting pretty cold here but I hope yall doing just fine.<br />
    And for my entry, Id like to preach how cute Sakura Miko(A VTuber) is we could make a religion out of her, just look at these screenshots-</p>
    <img src={image} alt={'Screenshot'} className={'entry-image'} />
    <p>well that was actually just a joke. I actually had a hard time thinking on what to write but I finally settled on talking about <i><strong>Pokmon</strong></i>.</p>

    <p>Have you guys ever played the first generation?<br />
    They started with the Pokmon Red and Green then Blue and Yellow came afterwards.<br />
    Though unfortunately Pokmon Greens international version wasnt released.<br />
    But heres the interesting part. They actually chose the colour red and green after the worlds most known plumbers. Yes, Mario and Luigi. Not really a mind-blowing fact but its pretty cool knowing that Mario actually took part in the game.</p>
    
    <p>Whats more, the title <i>Pokmon</i> <strong>Pocket Monsters</strong> itself has an interesting origin too.<br />
    The name was originally named <i>Capsule Monsters</i> which was inspired by <strong>gashapons</strong>. However, <i>capsule</i> was already trademarked which made things complicated so they decided to change the name.  One of the developers blurted out these balls(currently pokball/monster ball in jp) can be put in a coat pocket etc hmmm and everyone liked that thought and finally agreed on naming it Pocket Monsters(Pokmon for the international version because the word Pocket Monster was a lewd slang).</p>

    <p>Ill change the subject a bit though but I believe Pokmon is also one of the most well-known animes with Dragon Ball, One Piece and Naruto. Though Ive never watched them because theyre too long but surprisingly, I was able to watch Pokmon forever to the point that its probably the anime Ive watched the most. Not only that but Its also my most played game starting from Leaf Green Sapphire Diamond  Heart Gold White White2 Omega Ruby Ultra Sun overall with a whopping 5000hours. </p>

    <p>Whats so great about Pokmon is that the adventure part itself is so satisfying that that itself is worth the price and after beating the champion, the next step would be trying to capture the legendary pokmons(this stuff is also fun af). I personally like capturing these guys with a normal pokball for the swags and just because I didnt like the design of the masterball and ultraball. <img src={lul} className={'emote'} /> If anyone also beat the game and captured legendaries, congrats! You are a full-fledge casual player. To take it to the next level, there are also a lot of things to remember at first. PkRus(PokVirus), EV, IV, every Pokmons Base Stats, Bad-Statuses hidden effects etc. Its gonna take longer than I expected so lets leave it at there. So whats so great about it is there may be alot of things to remember at first but it also shows the great diversity you can do in a battle. Well indeed, strong pokmons are gonna be used alot and people use those against each other but trying to beat those with just a Zigzagoon? thats spicy. I really hope some of you get interested but just a warning, you would get rekt alot on your early attempts. I did. But <s>Im a masochist so it was not really bad</s> it made me notice how Pokmon can be really deep. Thats it for now.<br />
    Sorry for the wall of text and what I just wanted to say is</p>

    <p>I wish you all a Merry Christmas and a Happy New Year.<br />
    Hope everyones gonna be fine aaaand Im out.</p>

    <p>P.S. Corrections are gladly accepted. <img src={pekoraSalute} className={'emote'} /></p>
  </div>
  </>
)

export const metadata = {
  alias: '',
  username: '#0110',
  id: '297635569041801217',
  index: 10,
  date: 'December 10',
  icon: icon
}
